import React, { useState, useEffect } from 'react';
import { commerce } from './lib/commerce';
import StartPage from "./components/startPage.js";
import ConsentPage from "./components/ConsentPage.js";
import InBetween from "./components/InBetween";

import { Products, Navbar, Cart, Login, Main } from './components';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

const App = () => {
    let userInfo = {
        userName: "carman wong",
        emailAdress: "carman_wong@example.com",
        userID: 0,
        condition: 0,
    };



    const updateUserInfo = (adress, name) => {
        userInfo.userName = name;
        userInfo.emailAdress = adress;
    };

    const setUserID = (userID) => {
        userInfo.userID = userID;
    };

    const updateCondition = (condition, userID) => {
        userInfo.condition = condition;
        userInfo.userID = userID;
    }


    const [products, setProducts] = useState([]);
    const [cart, setCart] = useState({});

    const fetchProducts = async () => {
        const { data } = await commerce.products.list();

        setProducts(data);
    }


    const fetchCart = async () => {
        setCart(await commerce.cart.retrieve())
    }

    const handleAddToCart = async (productId, quantity) => {
        const cart = await commerce.cart.add(productId, quantity);

        setCart(cart.cart);
    }

    const handleUpdateCartQty = async (productId, quantity) => {
        const cart = await commerce.cart.update(productId, { quantity });

        setCart(cart)
    }


    const handleRemoveFromCart = async (productId) => {
        const { cart } = await commerce.cart.remove(productId);

        setCart(cart);
    }

    const handleEmptyCart = async () => {
        const { cart } = await commerce.cart.empty();

        setCart(cart);
    }

    useEffect(() => {
        fetchProducts();
        fetchCart();
    }, []);

    console.log(cart);

    return (
        <Router>

            <div>

                <Switch>
                    <Route path="/" exact render={() => <StartPage />} />

                    <Route
                        path="/ConsentPage"
                        render={() => (
                            <ConsentPage updatedData={updateUserInfo}></ConsentPage>
                        )}
                    />

                    <Route
                        path="/InBetween"
                        render={() => (
                            <InBetween setCondition={updateCondition}></InBetween>
                        )}
                    />

                    <Route exact path="/StoreClient">
                        <Navbar totalItems={cart.total_items} />
                        <Products products={products} onAddToCart={handleAddToCart} />
                    </Route>

                    <Route exact path="/main">
                        <Navbar totalItems={cart.total_items} />
                         <Main /> 
                    </Route>

                    <Route exact path="/sale">
                        <Navbar totalItems={cart.total_items} />
                        {/* <Sale />  */}
                    </Route>

                    <Route exact path="/newin">
                        <Navbar totalItems={cart.total_items} />
                        <Products products={products} onAddToCart={handleAddToCart} />
                    </Route>

                    <Route exact path="/tops">
                        <Navbar totalItems={cart.total_items} />
                        {/* <Tops />  */}
                    </Route>

                    <Route exact path="/bottoms">
                        <Navbar totalItems={cart.total_items} />
                        {/* <Bottoms />  */}
                    </Route>

                    <Route exact path="/accessories">
                        <Navbar totalItems={cart.total_items} />
                        {/* <Accessories />  */}
                    </Route>


                    <Route exact path="/cart">
                        <Navbar totalItems={cart.total_items} />
                        <Cart
                            cart={cart}
                            handleUpdateCartQty={handleUpdateCartQty}
                            handleRemoveFromCart={handleRemoveFromCart}
                            handleEmptyCart={handleEmptyCart}
                        />
                    </Route>

                    <Route exact path="/login">
                        <Navbar totalItems={cart.total_items} />
                        <Login />
                    </Route>

                </Switch>
            </div>
        </Router>
    )
}

export default App;