import React from 'react';
import { AppBar, Toolbar, IconButton, Badge, MenuItem, Menu, Typography } from '@material-ui/core';
import { ShoppingCart, AccountCircle} from '@material-ui/icons';
import { Link, useLocation } from 'react-router-dom';

import logo from '../../assets/commerce.png';

import useStyles from "./styles";

const Navbar = ({ totalItems }) => {
    const classes = useStyles();
    const location = useLocation();

    return (
        <>
            <AppBar position="fixed" className={classes.appBar} color="inherit">
                <Toolbar>
                    <Typography component={Link} to="/main" font-size="25px" variant="h3" className={classes.title} color="inherit">
                        DigitGoods.
                    </Typography>

                    <div className={classes.grow} />


                    {location.pathname !== '/cart' && (
                        <div className={classes.button}>
                              <IconButton component={Link} to="/login" aria-label="Account" color="inherit">
                              <Badge  color="primary">
                                    <AccountCircle />
                                </Badge>
                            </IconButton>

                            <IconButton component={Link} to="/cart" aria-label="Show cart items" color="inherit">
                                <Badge badgeContent={totalItems} color="secondary">
                                    <ShoppingCart />
                                </Badge>
                            </IconButton>
                        </div>

                    )}

                </Toolbar>

                <div className={classes.menu}  >
                    <IconButton className={classes.menuitem} component={Link} to="/sale" > Sale </IconButton>  
                    <IconButton className={classes.menuitem} component={Link} to="/newin"> New In </IconButton>
                    <IconButton className={classes.menuitem} component={Link} to="/tops"> Tops </IconButton>
                    <IconButton className={classes.menuitem} component={Link} to="/bottoms"> Bottoms </IconButton>
                    <IconButton className={classes.menuitem} component={Link} to="/accessories"> Accessories </IconButton>
                </div>


            </AppBar>

        </>
    )
}

export default Navbar